"use client";

import { useEffect, useRef, useState } from "react";

interface TrackMap2DProps {
  lapDataA: any;
  lapDataB: any;
  corners: any[];
  hoveredDistance: number | null;
  onHoverDistance: (distance: number | null) => void;
}

export function TrackMap2D({
  lapDataA,
  lapDataB,
  corners,
  hoveredDistance,
  onHoverDistance,
}: TrackMap2DProps) {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [tooltip, setTooltip] = useState<{ x: number; y: number; text: string } | null>(null);

  useEffect(() => {
    if (!canvasRef.current || !lapDataA?.telemetry) return;

    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    if (!ctx) return;

    const dpr = window.devicePixelRatio || 1;
    const rect = canvas.getBoundingClientRect();
    canvas.width = rect.width * dpr;
    canvas.height = rect.height * dpr;
    ctx.scale(dpr, dpr);

    const width = rect.width;
    const height = rect.height;
    const padding = 60;

    const telemetryA = lapDataA.telemetry;
    const telemetryB = lapDataB?.telemetry;

    const allX = telemetryA.map((p: any) => p.worldPositionX);
    const allZ = telemetryA.map((p: any) => p.worldPositionZ);

    const minX = Math.min(...allX);
    const maxX = Math.max(...allX);
    const minZ = Math.min(...allZ);
    const maxZ = Math.max(...allZ);

    const rangeX = maxX - minX;
    const rangeZ = maxZ - minZ;
    const scale = Math.min((width - padding * 2) / rangeX, (height - padding * 2) / rangeZ);

    const toScreenX = (x: number) => padding + (x - minX) * scale;
    const toScreenZ = (z: number) => height - (padding + (z - minZ) * scale);

    ctx.fillStyle = "#0a0a0a";
    ctx.fillRect(0, 0, width, height);

    ctx.strokeStyle = "#1a1a1a";
    ctx.lineWidth = 1;
    for (let i = 0; i <= 10; i++) {
      const x = padding + (i / 10) * (width - padding * 2);
      const y = padding + (i / 10) * (height - padding * 2);

      ctx.beginPath();
      ctx.moveTo(x, padding);
      ctx.lineTo(x, height - padding);
      ctx.stroke();

      ctx.beginPath();
      ctx.moveTo(padding, y);
      ctx.lineTo(width - padding, y);
      ctx.stroke();
    }

    corners.forEach((corner: any) => {
      if (corner.brakingZone) {
        const bz = corner.brakingZone;
        const startIdx = telemetryA.findIndex((p: any) => p.distance >= bz.entryDistance);
        const endIdx = telemetryA.findIndex((p: any) => p.distance >= bz.exitDistance);

        if (startIdx >= 0 && endIdx >= 0) {
          ctx.strokeStyle = "rgba(255, 60, 60, 0.6)";
          ctx.lineWidth = 12;
          ctx.lineCap = "round";
          ctx.lineJoin = "round";

          ctx.beginPath();
          for (let i = startIdx; i <= endIdx; i++) {
            const p = telemetryA[i];
            const x = toScreenX(p.worldPositionX);
            const z = toScreenZ(p.worldPositionZ);
            if (i === startIdx) {
              ctx.moveTo(x, z);
            } else {
              ctx.lineTo(x, z);
            }
          }
          ctx.stroke();
        }
      }
    });

    const maxSpeed = Math.max(...telemetryA.map((p: any) => p.speed));

    ctx.lineWidth = 8;
    ctx.lineCap = "round";
    ctx.lineJoin = "round";

    for (let i = 0; i < telemetryA.length - 1; i++) {
      const p1 = telemetryA[i];
      const p2 = telemetryA[i + 1];

      const speedRatio = p1.speed / maxSpeed;
      const hue = speedRatio * 120;
      ctx.strokeStyle = `hsla(${hue}, 100%, 50%, 0.9)`;

      ctx.beginPath();
      ctx.moveTo(toScreenX(p1.worldPositionX), toScreenZ(p1.worldPositionZ));
      ctx.lineTo(toScreenX(p2.worldPositionX), toScreenZ(p2.worldPositionZ));
      ctx.stroke();
    }

    if (telemetryB) {
      ctx.lineWidth = 4;
      ctx.strokeStyle = "rgba(255, 136, 68, 0.4)";

      ctx.beginPath();
      telemetryB.forEach((p: any, i: number) => {
        const x = toScreenX(p.worldPositionX);
        const z = toScreenZ(p.worldPositionZ);
        if (i === 0) {
          ctx.moveTo(x, z);
        } else {
          ctx.lineTo(x, z);
        }
      });
      ctx.stroke();
    }

    corners.forEach((corner: any, idx: number) => {
      const apexPoint = telemetryA.find(
        (p: any) => Math.abs(p.distance - corner.apexDistance) < 5
      );

      if (apexPoint) {
        const x = toScreenX(apexPoint.worldPositionX);
        const z = toScreenZ(apexPoint.worldPositionZ);

        ctx.fillStyle = "#000";
        ctx.beginPath();
        ctx.arc(x, z, 14, 0, Math.PI * 2);
        ctx.fill();

        ctx.fillStyle = "#fff";
        ctx.beginPath();
        ctx.arc(x, z, 12, 0, Math.PI * 2);
        ctx.fill();

        ctx.fillStyle = "#000";
        ctx.font = "bold 10px sans-serif";
        ctx.textAlign = "center";
        ctx.textBaseline = "middle";
        ctx.fillText(`${idx + 1}`, x, z);
      }
    });

    const startPoint = telemetryA[0];
    const sx = toScreenX(startPoint.worldPositionX);
    const sz = toScreenZ(startPoint.worldPositionZ);

    ctx.fillStyle = "#00ff88";
    ctx.beginPath();
    ctx.arc(sx, sz, 8, 0, Math.PI * 2);
    ctx.fill();

    ctx.strokeStyle = "#000";
    ctx.lineWidth = 2;
    ctx.stroke();

    ctx.fillStyle = "#666";
    ctx.font = "12px monospace";
    ctx.textAlign = "left";
    ctx.fillText("Speed: Low", padding, height - 10);
    ctx.fillStyle = "#00ff00";
    ctx.fillText("‚Üí", padding + 80, height - 10);
    ctx.fillStyle = "#ffff00";
    ctx.fillText("‚Üí", padding + 100, height - 10);
    ctx.fillStyle = "#ff0000";
    ctx.fillText("High", padding + 120, height - 10);

  }, [lapDataA, lapDataB, corners, hoveredDistance]);

  const handleMouseMove = (e: React.MouseEvent<HTMLCanvasElement>) => {
    if (!canvasRef.current || !lapDataA?.telemetry) return;

    const canvas = canvasRef.current;
    const rect = canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    const width = rect.width;
    const height = rect.height;
    const padding = 60;

    const telemetryA = lapDataA.telemetry;
    const minX = Math.min(...telemetryA.map((p: any) => p.worldPositionX));
    const maxX = Math.max(...telemetryA.map((p: any) => p.worldPositionX));
    const minZ = Math.min(...telemetryA.map((p: any) => p.worldPositionZ));
    const maxZ = Math.max(...telemetryA.map((p: any) => p.worldPositionZ));
    const rangeX = maxX - minX;
    const rangeZ = maxZ - minZ;
    const scale = Math.min((width - padding * 2) / rangeX, (height - padding * 2) / rangeZ);

    let closestPoint = null;
    let closestDist = Infinity;

    telemetryA.forEach((p: any) => {
      const sx = padding + (p.worldPositionX - minX) * scale;
      const sz = height - (padding + (p.worldPositionZ - minZ) * scale);
      const dist = Math.sqrt((x - sx) ** 2 + (y - sz) ** 2);

      if (dist < closestDist && dist < 20) {
        closestDist = dist;
        closestPoint = p;
      }
    });

    if (closestPoint) {
      onHoverDistance(closestPoint.distance);
      setTooltip({
        x: e.clientX,
        y: e.clientY,
        text: `${Math.round(closestPoint.distance)}m | ${Math.round(closestPoint.speed)} km/h`,
      });
    } else {
      onHoverDistance(null);
      setTooltip(null);
    }
  };

  const handleMouseLeave = () => {
    onHoverDistance(null);
    setTooltip(null);
  };

  return (
    <div className="relative rounded-xl border border-zinc-800 bg-zinc-900/50 p-6">
      <div className="mb-4 flex items-center justify-between">
        <h2 className="text-xl font-semibold text-white">Track Map</h2>
        <div className="flex items-center gap-4 text-sm">
          <div className="flex items-center gap-2">
            <div className="h-2 w-8 rounded bg-gradient-to-r from-red-500 via-yellow-500 to-green-500" />
            <span className="text-zinc-400">Racing Line (speed)</span>
          </div>
          <div className="flex items-center gap-2">
            <div className="h-2 w-8 rounded bg-red-500/60" />
            <span className="text-zinc-400">Brake Zones</span>
          </div>
        </div>
      </div>

      <canvas
        ref={canvasRef}
        className="w-full rounded-lg"
        style={{ height: "600px" }}
        onMouseMove={handleMouseMove}
        onMouseLeave={handleMouseLeave}
      />

      {tooltip && (
        <div
          className="pointer-events-none fixed z-50 rounded bg-black px-3 py-2 text-sm text-white shadow-lg border border-zinc-700"
          style={{
            left: tooltip.x + 10,
            top: tooltip.y + 10,
          }}
        >
          {tooltip.text}
        </div>
      )}
    </div>
  );
}
