"use client";

import { useState, useEffect } from "react";
import { TrackMap2D } from "./components/TrackMap2D";
import { TelemetryGraphs } from "./components/TelemetryGraphs";
import { LapSelector } from "./components/LapSelector";

export default function LapAnalysisPage() {
  const [availableLaps, setAvailableLaps] = useState<any[]>([]);
  const [selectedLapA, setSelectedLapA] = useState<number | null>(null);
  const [selectedLapB, setSelectedLapB] = useState<number | null>(null);
  const [lapDataA, setLapDataA] = useState<any>(null);
  const [lapDataB, setLapDataB] = useState<any>(null);
  const [corners, setCorners] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [hoveredDistance, setHoveredDistance] = useState<number | null>(null);

  useEffect(() => {
    fetch("/api/laps")
      .then((res) => res.json())
      .then((data) => {
        setAvailableLaps(data.laps);
        if (data.laps.length > 0) {
          setSelectedLapA(data.laps[0].lapNumber);
          if (data.laps.length > 1) {
            setSelectedLapB(data.laps[1].lapNumber);
          }
        }
        setLoading(false);
      })
      .catch((err) => {
        console.error("Failed to load laps:", err);
        setLoading(false);
      });
  }, []);

  useEffect(() => {
    if (selectedLapA === null) return;

    fetch(`/api/laps/${selectedLapA}`)
      .then((res) => res.json())
      .then((data) => {
        setLapDataA(data);
      })
      .catch((err) => console.error("Failed to load lap A:", err));
  }, [selectedLapA]);

  useEffect(() => {
    if (selectedLapB === null) return;

    fetch(`/api/laps/${selectedLapB}`)
      .then((res) => res.json())
      .then((data) => {
        setLapDataB(data);
      })
      .catch((err) => console.error("Failed to load lap B:", err));
  }, [selectedLapB]);

  useEffect(() => {
    if (!lapDataA?.metadata?.trackName) return;

    const trackName = lapDataA.metadata.trackName;
    const match = trackName.match(/\(([^)]+)\)/);
    const simpleName = match ? match[1].toLowerCase() : trackName.toLowerCase();

    fetch(`/api/corners/${simpleName}`)
      .then((res) => res.json())
      .then((data) => {
        setCorners(data.corners || []);
      })
      .catch((err) => console.error("Failed to load corners:", err));
  }, [lapDataA]);

  if (loading) {
    return (
      <div className="flex h-screen items-center justify-center bg-zinc-950">
        <div className="text-center">
          <div className="mb-4 text-4xl">‚è±Ô∏è</div>
          <div className="text-xl text-zinc-400">Loading telemetry data...</div>
        </div>
      </div>
    );
  }

  if (availableLaps.length === 0) {
    return (
      <div className="flex h-screen items-center justify-center bg-zinc-950">
        <div className="text-center">
          <div className="mb-4 text-4xl">üèéÔ∏è</div>
          <div className="mb-2 text-2xl text-white">No Laps Recorded</div>
          <div className="text-zinc-400">
            Start the UDP listener and drive some laps in F1 2024
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-zinc-950">
      <div className="border-b border-zinc-800 bg-zinc-900/50 backdrop-blur-sm">
        <div className="container mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-white">
                2D Lap Analysis
              </h1>
              <p className="text-sm text-zinc-400">
                {lapDataA?.metadata?.trackName || "Loading..."}
                {lapDataA?.metadata?.carName && ` ‚Ä¢ ${lapDataA.metadata.carName}`}
              </p>
            </div>

            <LapSelector
              availableLaps={availableLaps}
              selectedLapA={selectedLapA}
              selectedLapB={selectedLapB}
              onSelectLapA={setSelectedLapA}
              onSelectLapB={setSelectedLapB}
            />
          </div>
        </div>
      </div>

      <div className="container mx-auto px-6 py-6">
        <div className="grid gap-6 lg:grid-cols-2">
          <div className="lg:col-span-2">
            <TrackMap2D
              lapDataA={lapDataA}
              lapDataB={lapDataB}
              corners={corners}
              hoveredDistance={hoveredDistance}
              onHoverDistance={setHoveredDistance}
            />
          </div>

          <div className="lg:col-span-2">
            <TelemetryGraphs
              lapDataA={lapDataA}
              lapDataB={lapDataB}
              corners={corners}
              hoveredDistance={hoveredDistance}
              onHoverDistance={setHoveredDistance}
            />
          </div>
        </div>
      </div>
    </div>
  );
}
